<template>
  <div class="main-page">
  	<div class='header'>
  		<div class="header-img">
  			<img src="../assets/img/yoshino_small.jpg" alt="" />
  		</div>
  		<div class="header-content">
  			<div>
  				<h1>Yoshino的网络日志</h1>
  			</div>
  			<div class="header-quotations">  				
  				<p>-----请... 不要弄疼我</p>
  			</div>
  		</div>
  		<div class="header-search"> 				
  				<input type="text" /><button><i class='iconfont'></i></button>
  		</div>
  	</div>
  	<div></div>
  </div>
</template>

<script>
export default {
  name: 'Main'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header{
	border-bottom: 1px solid #fff;
	padding-top:.1rem ;
	padding-bottom:.1rem;
	position: relative;
}
.header .header-img{
	width:1.5rem;
	display:inline-block;
}
.header img{
	vertical-align: top;	
}
.header-content{
	display:inline-block;
	vertical-align: top;
}
.header-content h1{
	display: inline-block;
	font-size: .5rem;
	margin-left: 0;
	vertical-align: top;
}
.header-quotations{	
	margin-top:-.1rem
}
.header-content p{
	display: inline-block;
	font-size: .2rem;
	vertical-align: bottom;
}
.header-search{
	height:.2rem;
	display:inline-block;
	vertical-align: bottom;
	border:.01rem solid #ccc;
	border-radius:.1rem;
	background: #fff;
	position: absolute;
	bottom:.1rem;
	right:.1rem
}
.header-search input, .header-search button{
	vertical-align: top;
	margin:0;
	padding:0;
	border:hidden;
	height:100%;
	outline:none;
}
.header-search input{
	border-top-left-radius:.1rem;
	border-bottom-left-radius: .1rem;
	padding-left:.1rem
}
.header-search button{
	width:.25rem;
	border-radius: .1rem;
	background:#fff
}
.header-search button:hover{
	cursor:pointer
}
.header-search button i:before{
	content: "\e612";
}
.main-page{
	width:12rem;
	margin:0 auto;
}
</style>



